.profile_wrapper {
  padding-top: 70px;
}

.sign_in {
  margin-bottom: 50px;
}

.sign_in h2 {
  font-weight: normal;
  font-size: 25px;

  letter-spacing: 0.2px;
  margin-bottom: 100px;
  text-align: center;
}
.signin_form form {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: 70px;
}

.signin_form form p {
  margin-bottom: 5px;
}
.email_sign p,
.password_sign p,
.create_acc_form p {
  font-weight: 400;
  font-size: 14px;
  margin-bottom: 3px;
  color: #4b4c4bfd;
}

.email_sign input,
.password_sign input,
.create_acc_form input {
  width: 410px;
  height: 40px;
  outline: #098560fd;
  border-radius: 4px;
  border: 1px solid #098560fd;
  text-indent: 7px;
  font-size: 17px;
  color: #4b4c4bfd;
}

.email_sign input {
  margin-bottom: 32px;
}

.password_sign input {
  margin-bottom: 17px;
}

.remember_me {
  display: flex;
  align-items: center;
  margin-bottom: 35px;
}

.signin_button input {
  width: 100%;
  height: 47px;
  background: #098560fd;
  border-radius: 6px;
  cursor: pointer;
  border: none;
  color: #fff;

  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 2px;
  margin-bottom: 30px;
  transition: all 0.5s ease;
}
.submit_button {
  width: 410px !important;
}
.signin_button input:hover {
  transition: all 0.5s ease;
  background: #055f44fd;
}

.password_recovery p,
.password_recovery a {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #000000;

  text-align: center;
}
.password_recovery a {
  transition: all 0.5s ease;
  font-weight: 600;
}
.password_recovery a:hover {
  color: rgb(92, 91, 91);
}

.sign_in span {
  color: red;
  font-size: 12px;
  margin-top: 5px;
}
.login_links {
  margin-top: 8px;
}
.login_links a {
  margin: 0 8px;
}
.password_recovery a {
  transition: all 0.5s ease;
}
.password_recovery a:hover {
  opacity: 0.7;
  transition: all 0.5s ease;
}

/* create account */

.create_acc_form {
  margin-bottom: 40px;
}
.create_acc_form form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.create_acc_form p {
  margin-top: 30px;
  margin-bottom: 5px;
}
.create_acc h2 {
  text-align: center;
  font-weight: normal;
  margin-bottom: 60px;
}

.create_acc_form span {
  color: red;
  font-size: 12px;
  font-weight: normal;
  margin-top: 4px;
}

.submit_button {
  width: 100%;
  height: 47px;
  background: #098560fd;
  border-radius: 6px;
  cursor: pointer;

  color: #fff !important;
  border: none !important;
  outline: none;
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 2px;
  margin-top: 30px;
  transition: all 0.5s ease;
}

.submit_button:hover {
  transition: all 0.5s ease;
  background: #055f44fd;
}

.create_acc,
.sign_in {
  padding-bottom: 50px;
}

.autorizing_page {
  max-width: 410px;
  height: 100%;
  margin: 0 auto;
}

.signin_form input {
  margin-bottom: 0;
}

.sign_in p {
  margin-top: 20px;
}
.sign_in span {
  color: red;
}

.password_recovery {
  margin-top: 30px;
}

@media only screen and (max-width: 420px) {
  .profile_wrapper {
    padding-top: 30px;
  }
  .sign_in,
  .create_acc {
    margin-bottom: 20px;
    padding: 0 15px;
  }
  .sign_in h2,
  .create_acc h2 {
    font-size: 20px;
    margin-bottom: 50px;
  }
  .email_sign input,
  .password_sign input,
  .create_acc_form input {
    width: 100%;
  }
  .email_sign p,
  .password_sign p,
  .create_acc_form p,
  .password_recovery a {
    font-size: 11px;
  }
  .signin_button input,
  .submit_button {
    font-size: 15px !important;
  }
  .submit_button {
    width: 100% !important;
  }
  .signin_form {
    width: 100% !important;
  }
  .signin_form input {
    width: 100% !important;
  }
  .signin_form form {
    width: 100%;
  }
  .sign_in form div {
   width: 100%;
  }
  .signin_form {
    padding-bottom: 40px;
  }
}
